.player {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  position: relative;
  padding-bottom: 56.25%;
}

.player-video {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0; 
  right: 0;
}

.player-next-track {
  text-align: right;
  font-size: .9em;
  color: #999;
  padding-right: 1em;
}


