.player-controller {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: #fff;
  z-index: 10;
}

.player-controller-buttons {
  display: -ms-flexbox;
  display: flex;
}

.player-controller button {
  color: #555;
  padding: 0;
}

.player-controller-time,
.player-controller-duration {
  padding: 5px;
  color: #999;
  white-space: nowrap;
}

.player-seekbar {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
}

.prev-btn,
.next-btn {
  font-size: 1.5em;
  background: none;
  border: none;
  margin-left: .5em;
  margin-right: .5em;
}

.play-btn,
.pause-btn {
  font-size: 2em;
  background: none;
  border: none;
  padding: .5em;
}

.player-volume-icon {
  padding-left: 1em;
  padding-right: .2em;
}

.player-track {
  padding: 1em;
  text-align: center;
  -ms-flex: 1 1 1px;
      flex: 1 1 1px;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}


